<script>
	import { getUserInfo  } from "./Api/index.js"
	export default {
		onLaunch: function() {
			console.log('App Launch')
		},
		onShow: function() {
			console.log('App Show')
		},
		onHide: function() {
			console.log('App Hide')
		},
		
		created(){
			this.getTokenServer();
		},
		methods:{
			// 通过token获取个人信息  主要也是要拦截 tonken
			getTokenServer(){
				let token = uni.getStorageSync('user_token');
				if(token == undefined || token == '' || token.length<=0){
					uni.reLaunch({
						url:"./pages/login/login"
					})
					return 
				}
				getUserInfo(token,v => {
					if(v.data.code==200){
						this.$store.state.userInfo = v.data.data
						uni.setStorageSync('user_info',v.data.data);
					}else{
						// token 失效
						uni.reLaunch({
							url:"./pages/login/login"
						})
					}
				})
			}
		}
	}
</script>

<style>

	/*每个页面公共css */
	*{
		padding: 0;
		margin: 0;
		font-family: "AlibabaPuHuiTi" !important;
	}

	@font-face {
		font-family:"AlibabaPuHuiTi";
		src: url(/static/font/Alibaba-PuHuiTi-Regular.ttf);
	}
	
	button::after { border: none }
	

</style>
                                 